import { Box, Card, Chip, CircularProgress, Container, createStyles, Divider, InputAdornment, Link, makeStyles, TextField, Theme, Typography } from '@material-ui/core'
import Paper from '@material-ui/core/Paper'
import SearchIcon from '@material-ui/icons/Search'
import Head from "next/head"
import React, { useState } from "react"
import SearchResultsTable from "../components/SearchResultsTable"
import { getDdgResults, getGoogleResults } from "../utils/api"
import Header from '../components/Header'

const Index = () => {
    const [search, setSearch] = useState("")
    const [isLoading, setIsLoading] = useState(false)
    const [googleResults, setGoogleResults] = useState<SearchResult[] | undefined>(undefined)
    const [ddgResults, setDdgResults] = useState<SearchResult[] | undefined>(undefined)
    
    
    const runSearch = (search: string) => {
        if (search) {
            setIsLoading(true)
            setDdgResults(undefined)
            setGoogleResults(undefined)
            const g = getGoogleResults(search, 2).then(setGoogleResults)
            const d = getDdgResults(search).then(setDdgResults)
            Promise.all([g, d]).then(() => setIsLoading(false))
        }
    }
    const useStyles = makeStyles((theme: Theme) =>
        createStyles({
            root: {
                flexWrap: 'wrap',
                '& > *': {
                    margin: theme.spacing(0.5),
                },
            },
            suggestions: {
                display: 'inline-block',
                flexWrap: 'wrap',
                margin: theme.spacing(0.5),
                '& > *': {
                    fontSize: 14,
                    margin: theme.spacing(0.5),    
                }
            },
        }),
    )
    const classes = useStyles()

    const suggestions = {
        general: ["ddg", "google", "fb", "st patricks in 2022",  "date of ny marathon",],
        news: ["2008 crisis", "corona virus"],
        politics: ["trump", "sanders", "biden", "republicans", "democrats"],
        shopping: ["buy tv", "best cell phone", "flights"],
        conspiracy: ["are vacccines dangerous", "flat earth", "bush 9/11", "jfk assassination", "clinton body count", "moon landing"],
        entertainment: ["eminem fast song", "news guy spider man actor"],
        programming: ["css center text", "implicits scala 3.0", "regex match .com", "tsfiddle",],
    }
    return(
        <React.Fragment>
            <Head>
                <link rel="icon" type="image/x-icon" href="/favicon.ico" />
            </Head>
            <Container maxWidth="lg">
                <Header/>
                <Link href="/about">About</Link>
                <Divider />
                <Box my={4} style={{display: "inline-block"}}>
                    { Object.entries(suggestions).map(([category, terms]) =>
                        <Paper key={category} className={classes.suggestions}>
                            <Typography color="textSecondary" gutterBottom>
                                {category}
                            </Typography>
                            {terms.map(term =>
                                <Chip
                                    key={`${category}${term}`}
                                    style={{margin: 10}}
                                    label={term} onClick={() => {setSearch(term); runSearch(term)}}
                                />)
                            }
                        </Paper>
                    )}
                </Box>
                <Divider />
                <Box my={4}>
                    <Card className={classes.root}>
                        <TextField
                            autoFocus
                            placeholder="Search Term"
                            onChange={e => setSearch(e.target.value)}
                            value={search}
                            onKeyPress={(e) => e.key === "Enter" && runSearch(search)}
                            InputProps={{startAdornment: <InputAdornment position="start"><SearchIcon/></InputAdornment>}}
                        />
                    </Card>
                </Box>
                <Divider/>
                <Box my={4}>
                    {isLoading && <CircularProgress />}
                    {googleResults !== undefined && ddgResults !== undefined &&
                        <SearchResultsTable resultsLeft={googleResults} resultsRight={ddgResults}/>
                    }
                </Box>
            </Container>
        </React.Fragment>
    )
}

export default Index
